import React from "react";
import {Col, Container, ListGroupItem, Row, Table} from "react-bootstrap";
import {IDay, ILesson} from "../models";

interface LessonProps {
    lesson: ILesson
}

export function LessonCard(props: LessonProps) {


    return(
                <tr>
                    <td><small style={{fontSize: "12px"}}>Пара №</small><strong>{props.lesson.ring_time.number}</strong> <br/> <b>{props.lesson.ring_time.begin.slice(0, -3)}</b>  -  <b>{props.lesson.ring_time.end.slice(0, -3)}</b></td>
                    <td><small style={{fontSize: "12px"}}>Предмет:</small> <b>{props.lesson.subject}</b> <br/> <small style={{fontSize: "12px"}}>Преподаватель:</small> <b>{props.lesson.tutor.full_name}</b> <br/> <small style={{fontSize: "12px"}}>Группа:</small> <b>{props.lesson.group}</b> {props.lesson.subgroup != null &&
                        <small style={{fontSize: "12px"}}> Подгруппа: <b style={{fontSize: "17px"}}>{props.lesson.subgroup}</b></small>}</td>
                    <td width={"10px"}><small style={{fontSize: "12px"}}>Аудитория:</small> <b>{props.lesson.auditorium}</b></td>
                </tr>
    )

    function getTime(time: string) {
        let date = new Date(time)
        return `${date.getHours()}:${(date.getMinutes()<10?'0':'') + date.getMinutes() }`
    }
    
}